import React,{useState} from 'react'
import { Box } from '@material-ui/core';
import { TextField, Typography,Button } from '@mui/material';

function NewTransaction({addTransaction}) {
    const[myCss,setMyCss]=useState({
        "display":"flex",
        "flex-direction":"column",
    });
    const[arr,Arr]=useState({
        "margin-top":"30px"
    });


    const[text,setText]=useState('');
    const[amount,setAmount]=useState();

    const newTransaction=()=>{
        const transaction={
            id:Math.floor(Math.random()*1000),
            text:text,
            amount: + amount
        }
        addTransaction(transaction);
    }

    return (
        <Box style={myCss} >
            <Typography style={arr} variant='h5'>New Transaction </Typography>
            <TextField style={arr} variant='standard' label='Enter Expense '  onChange={(e)=>setText(e.target.value)} />
            <TextField style={arr} variant='standard' label='Total Amount'  onChange={(e)=>setAmount(e.target.value)} />
            <Button style={arr} onClick={newTransaction}  variant="contained">Add New Transaction</Button>           
        </Box>
    )
}

export default NewTransaction;