import React , {useState} from 'react';
import TransactionList from './TransactionList';
import { Box ,Typography,Divider,List} from '@material-ui/core'



function Transaction({transactions,deleteTransaction}) {
const[myCss,setMyCss]=useState({
        'margin-bottom':'10px'
})

    return ( 
            <Box>
                <Typography style={myCss} variant="h5">Transactions History</Typography>
                <Divider style={myCss}/>
                <List>
                    {
                        transactions.map(transaction =>{
                            return <TransactionList key={transaction.id}  transaction={transaction}  deleteTransaction={deleteTransaction}/>;
                        })
                    }
                </List>
            </Box>
    
    )
}

export default Transaction
