import { Typography , Box } from "@mui/material";
import { makeStyles } from "@mui/styles";
import Balanace from "./Component/Balance";
import Cards from "./Component/Cards";
import NewTransaction from "./Component/NewTransaction";
import Transaction from "./Component/Transaction";
import React ,{useState} from 'react';
import "./App.css";

const useStyles = makeStyles({
  root: {
    color: "blue",
    margin: "10px 0",
    textTransform: "uppercase",
  },
  component: {
    background: "#fff",
    width: 800,
    padding: 10,
    borderRadius: 20,
    display:'flex',
    ' & > * ':{
      width:'50%',
      padding:10,
      height:'70vh'
    }
  },
});

function App() {

  const[transactions , setTransactions]=useState([
    {id : 1, text:'Momos' ,amount : -20},
    {id : 2, text:'Salary' ,amount : 30000},
    {id : 3, text:'Mukesh' ,amount : -200},
    {id : 4, text:'Bonus' ,amount : 15000}
  ]);

  const deleteTransaction=(id)=>{
    setTransactions(transactions.filter(transaction => transaction.id!==id)); 
  }

  const addTransaction=(transaction)=>{
    setTransactions(transactions=>[transaction ,...transactions]);
  }

  const classes = useStyles();
  return (
    <div className="App">
      <Typography className={classes.root} style={{ fontSize: 35 }}>
        Expense Tracker
      </Typography>
      <Box className={classes.component}>
        <Box>
          <Balanace transactions={transactions} />
          <Cards transactions={transactions}/>
          <NewTransaction addTransaction={addTransaction} />
        </Box>
        <Box>
          <Transaction transactions ={transactions} deleteTransaction={deleteTransaction}/>
        </Box>
      </Box>
    </div>
  );
}

export default App;




