import React from 'react';
import { makeStyles } from '@mui/styles';
import { ListItem,ListItemIcon,ListItemText } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';

const useStyle=makeStyles({
    list:{
        marginTop:10,
        border:"1px solid #f6f6f6"
    }
})




function TransactionList({transaction,deleteTransaction}) {

    const classes=useStyle();
    const color= transaction.amount >=0?'lightgray':'gray';
    const sign=transaction.amount >0 ?'₹' :'-₹';
    const amount=sign +Math.abs( transaction.amount);
    return (
        <ListItem className={classes.list} style={{background:`${color}`, color:'#111111'}}>
            <ListItemText primary={transaction.text}/>
            <ListItemText primary={amount}/>
           <ListItemIcon>
               <DeleteIcon  onClick={()=>deleteTransaction(transaction.id)}/>
           </ListItemIcon>
        </ListItem>
    )
}

export default TransactionList;
